<template>
  <div class="app" :class="mode">
    <navbar class="nav-bar" />
    <toggle :mode="mode" @toggle="toggle" />
    <router-view />
  </div>
</template>
<script>
import navbar from "@/components/navbar.vue";
import toggle from "@/components/toggle.vue";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      mode: "light",
    };
  },
  components: {
    navbar,
    toggle,
  },
  methods: {
        ...mapActions(["fetchWeather"]),
//toggle切換
    toggle() {
      if (this.mode === "dark") {
        this.mode = "light";
      } else {
        this.mode = "dark";
      }
    },

  } ,
   async mounted() {
     //558152
     //[lat ,lon]
    this.fetchWeather([24.1469, 120.683899, "台中,Taiwan"]);
  },
 
};
</script>


<style lang="scss">
@import './assets/scss/all';
</style>


