<template>
  <div class="home">
<b-container fluid v-if="weather" class="mt-4">
  <b-row>
    <!--當前狀態-->
     <b-col cols="12" sm="4" class="current">
        <search-bar class="mt-1"></search-bar>
        <div class="region mt-3">{{ weather.name }}</div>
        <div class="text-center font-weight-bold mt-1 time">
          {{ weather.current.dt | dateConvFull }} 
        </div>
         <!--天氣icon -->
            <weather-icon class="current-icon"></weather-icon>
            <div class="weather-status font-weight-bolder">
              {{ weather.current.weather[0].description }}
            </div>
            <div class="temperature text-center">
              <span class="temp-value">
                <span v-if="weather != undefined">{{
                  Math.round(weather.current.temp)
                }}</span>
              </span>
              <span class="unit"><sup>&#8451;</sup></span>
              <span class="unit" v-if="tempUnit"><sup>&#8457;</sup></span>
            </div>
           
             <p class="font-weight-bolder">
                體感溫度： {{ Math.round(weather.current.feels_like) }}&#8451;
            </p>
           
     </b-col>
    
    <b-col  sm="8">
       <div class="week-title mt-2"><b>每日統計</b></div>
        <b-row>
          <!--日出和日落-->
          <div cols="6" class="samile-5">
              <div class="title"><b>日出和日落</b></div> 
               <img src="@/assets/sun.png" class="img-fluid" alt="日出和日落" />
               <div>
                    <span class="sunrise-t font-weight-bold">{{
                      weather.current.sunrise | wkdayConvFull
                    }}</span>
               </div>
          </div>
           <!--紫外線指數-->
          <div class="samile-5">
              <div class="title"><b>紫外線指數</b></div> 
               <img src="@/assets/UV.png" class="img-fluid" alt="紫外線指數" />
                <div>
                 <span class="uvi"
                        ><b>{{ Math.round(weather.current.uvi) }}</b></span
                      >
                </div> 
          </div>    
             <!--濕度-->
          <div cols="6" class="samile-5">
              <div class="title"><b>濕度</b></div> 
               <img src="@/assets/humidity.png" class="img-fluid" alt="濕度" />
                <div>
                 <span class="font-weight-bold w-speed">{{
                    weather.current.humidity
                  }}
                  </span>
                  <span class="unt">%</span>
               </div>
          </div>
         
          <!--風力統計-->
          <div cols="6" class="samile-5">
              <div class="title"><b>風力統計</b></div> 
               <img src="@/assets/wind-stat.png" class="img-fluid" alt="風力統計" />
                <div>
                  <span class=""><b>{{
                    Math.round(weather.current.wind_speed * 10) / 10
                  }}</b></span
                  ><span class="unt">m/s</span>
                </div> 
          </div>
          
          <!--能見度-->
          <div cols="6" class="samile-5">
              <div class="title"><b>能見度</b></div> 
               <img src="@/assets/visibility.png" class="img-fluid" alt="能見度" />
               <div>
                  <span class="font-weight-bold w-speed">{{
                    Math.round(weather.current.visibility * 10) / 10
                  }}</span
                  >
                  <span class="unt">m</span>
               </div> 
          </div>
             
          

        </b-row>

        <div class="week-title mt-5"><b>每週天氣預報</b></div>
          <b-row>
              <div class="samile-7" v-for="forecast in weather.daily" :key="forecast.dt">
                 <forecast
                              :day="forecast.dt | dayConvFull"
                              :main="forecast.weather[0].main"
                              
                              :temp="Math.round(forecast.temp.day * 10) / 10"
                              :evening_temp="Math.round(forecast.temp.eve * 10) / 10"
                            />
              </div>
          </b-row>
    </b-col>
  </b-row>
</b-container>
  </div>
</template>

<script>
import searchBar from "@/components/searchBar.vue";
import Footer from "@/components/footer.vue";
import weatherIcon from "@/components/weather-icon.vue";
import forecast from "@/components/forecast.vue";

import { mapGetters, mapActions } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      tempUnit: false,
      compForecasts: [],

    };
  },
  components: {
    searchBar,
    weatherIcon,
    forecast,
    Footer,   
  },
  methods: {
    //註：store/modules/weather.js 內 actions / fetchWeather
    ...mapActions(["fetchWeather"]),
  },
  computed: { 
    ...mapGetters({ weather: "allWeather" }) 
  },
};
</script>
<style lang="scss">
/* dark mode styling */

.dark {
  .current {
    .search-bar {
      border: 4px solid rgba(255, 255, 255, 0.427) !important;
      transition: 0.35s ease-out;

      input {
        background-color: rgba(255, 255, 255, 0.8) !important;
        transition: 0.35s ease-out;
      }
    }
  }

  .highlight {
    background-color: rgb(10, 29, 44);
    transition: 0.35s ease-out;

    .stat {
      background: #162736;
      transition: 0.35s ease-out;
    }
  }
}
/*  */
</style>



