<template>
 <b-container fluid v-if="weather" class="about mt-4">
    <b-row>
      <div class="col-12 col-sm-4 offset-sm-4">
        <div class="title"><h1>關於我</h1></div>
        <div class="col-12 name">黃瑄惠</div>
     
      <div class="col-12 mobile">行動電話：0918-329245</div>
     <div class="col-12 email">電子信箱：lara1105huang@gmail.com</div>
     <div class="col-12 email">這是使用Vue Cli Vuex Axios 接天氣Ａpi 所撰寫</div>
      </div>
    </b-row>
</b-container>
</template>

<script>
import searchBar from "@/components/searchBar.vue";
import Footer from "@/components/footer.vue";
import weatherIcon from "@/components/weather-icon.vue";
import forecast from "@/components/forecast.vue";

import { mapGetters, mapActions } from "vuex";
export default {
  name: "About",
  data() {
    return {
      tempUnit: false,
      compForecasts: [],

    };
  },
  components: {
    searchBar,
    weatherIcon,
    forecast,
    Footer,   
  },
  methods: {
    ...mapActions(["fetchWeather"]),
  },
  computed: { ...mapGetters({ weather: "allWeather" }) },
};
</script>

<style lang="scss">
  .about{
     margin-top:15px;
    padding-top:40px;
    height: 768px;
    h1,.title{
      position: relative;
       font-size: 18px;
       font-weight: bolder;
       margin-bottom:50px;
       text-align: center;
    }
    .title::after {
      content: '';
      position: absolute;
      width: 40px;
      height: 2px;
      background-color: #552cde;
      top: 30px;
      left: 45.5%;
    }
   
    .name,.mobile,.email{
      font-size: 16px;
      text-align: left;
    }
  }
</style>


