import Vue from "vue";

Vue.filter("dateConvFull", function(value) {
 const milliseconds = value * 1000
 const dateObject = new Date(milliseconds)
 /* const humanDateFormat = dateObject.toLocaleString() //2019-12-9 10:30:15
console.log(dateObject) */

const day=dateObject.toLocaleString("zh-TW", {weekday: "long"}) ;// Monday
//en-US
//參考：https://www.w3schools.com/jsref/jsref_tolocalestring.asp
 const time=dateObject.toLocaleString("zh-TW", { timeStyle: "short"});
console.log( )
 // 10 AM, return `${time} ,${day} (GMT+1)`
 return `${time} ,${day}`
});

Vue.filter("dayConvFull", function(value) {
    const milliseconds = value * 1000
    const dateObject = new Date(milliseconds)
    /* const humanDateFormat = dateObject.toLocaleString() //2019-12-9 10:30:15
   console.log(dateObject) */
   
   const day=dateObject.toLocaleString("zh-TW", {weekday: "long"}) ;// Monday   
    return `${day}`
   });

   Vue.filter("wkdayConvFull", function(value) {
    const milliseconds = value * 1000
    const dateObject = new Date(milliseconds)
    /* const humanDateFormat = dateObject.toLocaleString() //2019-12-9 10:30:15
   console.log(dateObject) */ 
   const time=dateObject.toLocaleString("zh-TW", { timeStyle: "short"});
   console.log( )   
    return `${time}`
   });


