<template>
  <b-container class="mt-4">
<b-row>
    <b-col cols="12" sm="12">
        <h1>預測細節</h1>
        <div class="mt-1">
          {{ weather.daily[i].dt | dateConvFull }}
        </div>
        <div class="mt-1 mb-1">{{ weather.name }}</div>
          <div class="forecast-content">
                <b-row>
                  <!--日出和日落-->
                  <b-col cols="6" sm="3">
                    <div class="detal-content">
                      <div class="title"><b>日出和日落</b></div> 
                      <img src="@/assets/sun.png" class="img-fluid" alt="日出和日落" />
                      <div>
                      <div class="h14">
                      {{weather.daily[i].sunrise | wkdayConvFull}}
                      </div>
                      <div class="h14">
                      {{weather.daily[i].sunset | wkdayConvFull}}
                      </div>
                      </div>
                    </div>
                  </b-col>
                    <!--紫外線指數-->
                  <b-col cols="6" sm="3">
                    <div class="detal-content">
                      <div class="title"><b>紫外線指數</b></div> 
                      <img src="@/assets/UV.png" class="img-fluid" alt="紫外線指數" />
                        <div>
                        <span class="uvi">
                          <b>
                          {{ Math.round(weather.daily[i].uvi) }}
                          </b>
                        </span>
                        </div>
                    </div> 
                  </b-col> 
                          <!--濕度-->
                  <b-col cols="6" sm="3">
                    <div class="detal-content">
                      <div class="title"><b>濕度</b></div> 
                      <img src="@/assets/humidity.png" class="img-fluid" alt="濕度" />
                        <div>
                        <span class="font-weight-bold w-speed">
                          {{weather.daily[i].humidity}}
                          </span>
                          <span class="unt">%</span>
                      </div>
                    </div>
                  </b-col>
                
                <!--風力統計-->
                  <b-col cols="6" sm="3">
                    <div class="detal-content">
                    <div class="title"><b>風力統計</b></div> 
                  <img src="@/assets/wind-stat.png" class="img-fluid" alt="風力統計" />
                    <div>
                      <span class="font-weight-bold w-speed">{{
                        Math.round(weather.daily[i].wind_speed * 10) / 10
                      }}</span>
                    <span class="unt">m/s</span>
                    </div> 
                    </div>
                  </b-col>
                
                  <!--能見度-->
                  <div class="samile-5 hidden">
                      <div class="title"><b>能見度</b></div> 
                      <img src="@/assets/visibility.png" class="img-fluid" alt="能見度" />
                      <div>
                          <span class="font-weight-bold w-speed">
                            {{ Math.round(weather.daily[i].visibility * 10) / 10000 }}
                          </span>
                          <span class="unt">km</span>
                      </div> 
                  </div>
          
                </b-row>
              </div>
    </b-col>


    <b-col cols="12" sm="12" class="mt-2">
      <div class="mb-1"> <b>天氣預報</b></div>
        <div class="forecast-content">
               <b-row>
                <b-col cols="6" sm="2" @click="activate(1)" :class="{ active: active_el == 1 }">
                  <forecast
                      :day="weather.daily[0].dt | dayConvFull"
                      :main="weather.daily[0].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[0].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[0].temp.eve * 10) / 10"
                    />
                </b-col>
                <b-col cols="6" sm="2" @click="activate(2)" :class="{ active: active_el == 2 }">
                  <forecast
                      :day="weather.daily[1].dt | dayConvFull"
                      :main="weather.daily[1].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[1].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[1].temp.eve * 10) / 10"
                    />
                </b-col>
                <b-col cols="6" sm="2" @click="activate(3)" :class="{ active: active_el == 3 }">
                    <forecast
                      :day="weather.daily[2].dt | dayConvFull"
                      :main="weather.daily[2].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[2].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[2].temp.eve * 10) / 10"
                    />
                </b-col>
                <b-col cols="6" sm="2" @click="activate(4)" :class="{ active: active_el == 4 }">
                    <forecast
                      :day="weather.daily[3].dt | dayConvFull"
                      :main="weather.daily[3].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[3].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[3].temp.eve * 10) / 10"
                    />
                </b-col>
                <b-col cols="6" sm="2" @click="activate(5)" :class="{ active: active_el == 5 }">
                    <forecast
                      :day="weather.daily[4].dt | dayConvFull"
                      :main="weather.daily[4].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[4].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[4].temp.eve * 10) / 10"
                    />
                </b-col>
                <b-col cols="6" sm="2" @click="activate(6)" :class="{ active: active_el == 6 }">
                    <forecast
                      :day="weather.daily[5].dt | dayConvFull"
                      :main="weather.daily[5].weather[0].main"
                      class="fc"
                      :temp="Math.round(weather.daily[5].temp.day * 10) / 10"
                      :evening_temp="Math.round(weather.daily[5].temp.eve * 10) / 10"
                    />
                </b-col>
              </b-row>
        </div>
    </b-col>
</b-row>
  </b-container>
</template>

<script>
import { mapGetters } from "vuex";
import forecast from "@/components/forecast";
export default {
  data() {
    return {
      active_el: 1,
      UvIndex: 7,
      i: 0,
    };
  },
  components: {
    forecast,
  },
  methods: {
    activate(el) {
      this.active_el = el;
      this.i = el - 1;
    },
  },
  computed: mapGetters({ weather: "allWeather" }),
};
</script>



